import { Component } from "react";

class Projects extends Component {
  details = {};
  constructor(props) {
    super(props);
    this.state = {
      projectCount: 1,
      projects: [],
    };
    if (this.props.details.length) {
      this.state = {
        projectCount: this.props.details.length,
        projects: this.props.details,
      };
    }
    this.clearDetails();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.formReset != this.props.formReset) {
      if (this.props.formReset) {
        this.resetForm();
      }
    }
  }

  addProject() {
    let count = this.state.projectCount + 1;
    this.setState({
      projectCount: count,
    });
    this.clearDetails();
  }

  clearDetails() {
    this.details = {
      projectName: "",
      description: "",
    };
  }

  updateDetails(fieldName, val, i) {
    const projects = this.state.projects;
    if (projects[i]) {
      this.details = { ...projects[i] };
    } else {
      this.clearDetails();
    }
    this.details[fieldName] = val;

    if (projects.length && this.state.projectCount == projects.length) {
      projects.splice(i, 1, this.details);
    } else {
      projects.push({ ...this.details });
    }
    this.setState({
      projects,
    });
    this.props.onProjectsChange(this.state.projects);
  }

  deleteProject(index) {
    let projects = this.state.projects;
    let projectCount = this.state.projectCount;
    projectCount -= 1;
    projects.splice(index, 1);
    this.setState({ projects, projectCount });
  }

  resetForm() {
    this.setState({
      projectCount: 1,
      projects: [this.details],
    });
  }

  render() {
    return (
      <div
        className="accordion mb-3 col-9 col-sm-12 col-md-12 col-lg-9"
        id="ProjectsSection"
      >
        <div className="accordion-item">
          <h2 className="accordion-header" id="projectsHeading">
            <button
              className="accordion-button"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#projects"
              aria-expanded={this.props.isOpen}
              aria-controls="projects"
            >
              Projects
            </button>
          </h2>
          <div
            id="projects"
            className={`accordion-collapse collapse ${
              this.props.isOpen ? "show" : ""
            }`}
            aria-labelledby="projectsHeading"
            data-bs-parent="#ProjectsSection"
          >
            <div className="accordion-body">
              {[...Array(this.state.projectCount)].map((e, i) => (
                <div key={i}>
                  <div
                    className={`border rounded p-3 ${
                      this.state.projectCount < 2 ? "mb-2" : "mb-3"
                    }`}
                  >
                    {/* Project Name */}
                    <div className="row mb-3">
                      <label className="form-label col-2">Project Name</label>
                      <div className="col-7">
                        <input
                          className="form-control"
                          type="text"
                          value={this.state.projects[i]?.projectName}
                          onChange={(event) =>
                            this.updateDetails(
                              "projectName",
                              event.target.value,
                              i
                            )
                          }
                        />
                      </div>
                    </div>
                    {/* Description */}
                    <div className="row">
                      <label className="form-label col-2">Description</label>
                      <div className="col-7">
                        <textarea
                          className="form-control"
                          value={this.state.projects[i]?.description}
                          onChange={(event) =>
                            this.updateDetails(
                              "description",
                              event.target.value,
                              i
                            )
                          }
                        ></textarea>
                      </div>
                    </div>
                  </div>
                  {this.state.projectCount > 1 ? (
                    <div className="d-flex justify-content-end mb-3">
                      <button
                        className="btn btn-danger"
                        onClick={() => this.deleteProject(i)}
                      >
                        Delete
                      </button>
                    </div>
                  ) : null}
                </div>
              ))}
              <button
                className="btn btn-primary me-3"
                onClick={() => this.addProject()}
              >
                Add Project
              </button>

              <button
                className="btn btn-secondary"
                onClick={() => this.resetForm()}
                data-bs-toggle="tooltip" data-bs-placement="top" title="This will reset the projects form"
                disabled={
                  JSON.stringify(this.state.projects[0]) ==
                  JSON.stringify(this.details)
                }
              >
                Clear Projects form
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Projects;
